body {
    margin: 0;
}

img {
    width: 90%;
    height: 90%;
    object-fit: contain;
}

#Page {
    display: flex;
}

#menu {
    position: fixed;
    font-weight: bold;
    color: white;
    background-color: rgb(60, 60, 60);
    min-width: 200px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#menu * {
    text-align: center;
    min-width: 200px;
}

.main {
    margin-left: 200px;
    flex-grow: 1;
}

#allgallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    gap: 10px;
}

#allgallery>div {
    display: flex;
    justify-content: center;
    align-items: center;
}

#aboutmain {
    margin-top: calc(50vh - 270px);
    margin-left: 300px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    line-height: 30px;
    font-size: 15px;
    font-weight: bold;
}

#costmain {
    margin-top: calc(50vh - 179px);
    margin-left: 300px;
    font-weight: bold;
}

#yeargallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    gap: 10px;
}

/* #yeargallery>div>div {
    display: flex;
    justify-content: center;
    align-items: center;
} */

#typeselect {
    margin-left: 100px;
    margin-right: 100px;
    margin-top: 20px;
}

#typegallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    gap: 10px;
}

#contactmain {
    margin-top: calc(50vh - 124.5px);
    margin-left: 300px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    line-height: 30px;
    font-size: 15px;
    font-weight: bold;
}

#contactmain>* {
    text-decoration-line: none;
    color: black;
}

.popupclose {
    position: fixed;
    left: calc(50% + 240px);
    top: calc(50% - 270px);
    opacity: 0.5;
}

.popupcontent {
    position: fixed;
    left: calc(50% - 300px);
    top: calc(50% - 300px);
    width: 600px;
    height: 600px;

    background: white;
    border: 1px solid #d1d8dd;
    box-shadow: 0 0 6px 1px rgb(0 0 0 / 30%);
}

.shadow {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}